  font-family: "Times New Roman", Times, serif;
  color: white;
}

html,
body {
  margin: 0;
  padding: 0;
}

body {
  background: url(${___CSS_LOADER_URL_REPLACEMENT_0___}) no-repeat center center fixed;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
}

#main-content {
  height: 100vh;
  display: grid;
  grid-template: auto 2fr / 1fr;
  align-items: flex-start;
}

.nav-bar {
  max-height: 10%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 2em;
  background-color: #0284c780;
  border-radius: 0 0 1em 1em;
}

.content {
  align-self: center;
  justify-self: center;

  display: grid;
  grid-template: 1fr 1fr / auto;
  gap: 1em;
  padding: 1em;
  min-width: 70%;
  max-width: 1000px;
}

.content .current-day-content,
.content .forecast-content .forecast-card {
  padding: 1em;
  display: flex;
  gap: 1em;
  background-color: rgba(22, 52, 65, 0.5);
  border-radius: 1em;
  justify-content: space-around;
}

.content .forecast-content {
  display: grid;
  gap: 1em;
  grid-template-columns: repeat(auto-fit, minmax(400px, 1fr));
}

.content .current-day-content {
  font-size: large;
  .left-content {
    display: flex;
    flex-direction: column;
    align-items: center;

    .weather-icon {
      width: 128px;
      height: 128px;
    }
  }
}
.content .forecast-content .forecast-card {
  font-size: medium;

  .left-content {
    display: flex;
    flex-direction: column;
    align-items: center;
    .weather-icon {
      width: 48px;
      height: 48px;
    }
  }

  .right-content {
    display: flex;
    flex-direction: column;
    gap: 1em;
    justify-content: center;
    align-items: center;
    .graph-container {
      background-color: #082f49;
      padding: 1em;
      border-radius: 1em;
    }
  }
}
